import React from 'react';
import { Link } from 'react-router-dom';

function Header() {
    return (
        <header style={headerStyle}>
            <h1>ToDo List</h1>
            <Link style={linkStyle} to="/">Home</Link>
            <Link style={linkStyle} to="/about">About</Link>
        </header>
    );
}

const headerStyle = {
    textAlign: 'center',
    background: '#333',
    color: '#fff',
    padding: '8px',
    fontFamily: 'Courier'
}

const linkStyle = {
    color: '#fff',
    textDecoration: 'none',
    fontFamily: 'Tahoma',
    margin: '5px'
}

export default Header;